import React from 'react';

const cardContainer = {
	width: "126px",
	height: "150px",
	textAlign: "center",
	display: "flex",
	flexDirection: "column",
	border: "solid",
	borderRadius: "7px",
	backgroundColor: "#2f88fc",
	lineHeight: "18px"
}

const tempStyle = {
	fontSize: '30px',
	fontWeight: 'bold',
	lineHeight: '30px',
}

const cityCountryStyle = {
	lineHeight: "16px"
}

const dateTimeStyle = {
	fontSize: '16px',
}

/*const weatherIcon = {
	align: "center"
}*/

export default class FiveDayCard extends React.Component {
	render(){
		const timeDate = this.props.time;
		const timeData = typeof timeDate==="string" ?timeDate.split(" ")[1]:"";
		const dateData = typeof timeDate==="string" ?timeDate.split(" ")[0]:"";
		let month = dateData.slice(0, -3);
		month = month.substr(5);
		const date = dateData.substr(8);
		let time = timeData.slice(0, -6);
		let dateTag = '';
		switch(time) {
			case '00':
				time = "12am";
				break;
			case '03':
				time = "3am";
				break;
			case '06':
				time = "6am";
				break;
			case '09':
				time = "9am";
				break;
			case '12':
				time = "12pm";
				break;
			case '15':
				time = "3pm";
				break;
			case '18':
				time = "6pm";
				break;
			case '21':
				time = "9pm";
				break;
			default: 
				time = "No Time";
				break;
		}
		
		switch(typeof date==="string" ?date.split("")[1]:"") {
			case '1':
				dateTag = 'st';
				break;
			case '2':
				dateTag = 'nd';
				break;
			case '3':
				dateTag = 'rd';
				break;
			default:
				dateTag = 'th';
				break;
		}
		
		switch(month) {
			case '01':
				month = "Jan";
				break;
			case '02':
				month = "Feb";
				break;
			case '03':
				month = "March";
				break;
			case '04':
				month = "April";
				break;
			case '05':
				month = "May";
				break;
			case '06':
				month = "June";
				break;
			case '07':
				month = "July";
				break;
			case '08':
				month = "Aug";
				break;
			case '09':
				month = "Sept";
				break;
			case '10':
				month = "Oct";
				break;
			case '11':
				month = "Nov";
				break;
			case '12':
				month = "Dec";
				break;
			default: 
				month = '';
				break;
		}
		
		return(
			<div>	
				{ this.props.temperature && <div style={cardContainer}> 
					{ this.props.city && this.props.country && <p style={cityCountryStyle}>{this.props.city}, {this.props.country}</p> }
					{ this.props.time && <p style={dateTimeStyle}>{month} {date}{dateTag}</p> }
					{ this.props.time && <p style={dateTimeStyle}>{time}</p> }
					{ this.props.temperature && <p style={tempStyle}>{this.props.temperature}°</p> }
					{ this.props.description && <p>{this.props.description}</p> }
					{ this.props.error && <p> {this.props.error} </p> }
				</div> }
			</div>
		);
	}
} 
	
